@import '../../../common/styles/sass/variables';

.projectBlock {
    width: 400px;
    background-color: $secondBackgroundColor;
    margin-bottom: 20px;

    .image {
        display: flex;
        align-items: center;
        justify-content: center;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        position: relative;
        height: 250px;

        &:before {
            content: "";
            position: absolute;
            background-color: #000;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            opacity: 0.4;
        }

        .btn {
            z-index: 1;

            a {
                color: $textColor;
                text-decoration: none;
            }
        }
    }

    .titleContainer {
        width: 80%;
        margin: 0 auto;

        .title {
            font-size: 20px;
            font-weight: 600;
            letter-spacing: .1px;
            line-height: 1.5;
            margin-bottom: 6px;
            color: $textColor;
        }

        .description {
            color: $descriptionTextColor;
            font-size: 14px;
            font-weight: 400;
            line-height: 1.95;
        }

    }

}



